

<script>

export default {
  name: "mangaKuBaseLogic",

  methods:{
    async generalDeleteManga(manga_name,manga_id,costomLoading,passNotification,errorNotification) {
      await this.$http.delete("dogemanga/deletemanga", {
        data: { manga_id: manga_id }
      }).then((data) => {
        let res = data.data;
        costomLoading.close();
        if (res.code === 200) {
          if (passNotification) {
            passNotification(manga_name);
          }
          this.$emit("reload");
        } else if (res.code === 500) {
          if (errorNotification) {
            errorNotification(manga_name);
          }
        }
        // TODOedge casehandle
      }).catch((error) => {
        throw error
      })
    },

    async generalDownloadSwitch(manga_name,manga_id,costomLoading,passNotification,notFoundNotification) {
      await this.$http.post("dogemanga/downloadswitch", {
        manga_id: manga_id,
      }).then((data) => {
        let res = data.data;
        costomLoading.close();
        if (res.code === 200) {
          if (passNotification) {
            passNotification(manga_name, res.data.currentDownloadStatus);
          }
          this.$emit("reload");
        } else if (res.code === 424) {
          if (notFoundNotification) {
            notFoundNotification(manga_name);
          }
        }
      }).catch((error) => {
        throw error
      })
    },

    generateDeleteInitialInfo(item){
      return `<p>ID  <strong>${item.manga_id}</strong></p>` +
      `<p>  <strong>${item.manga_name}</strong></p>` +
      `<p>  <strong>${item.last_epi_name}</strong></p>` +
      `<p></p>`+
      `<p><strong></strong></p>`
    },

    generateDownloadSwitchInitialInfo(item){
      let info = `<p>ID  <strong>${item.manga_id}</strong></p>` +
          `<p>  <strong>${item.manga_name}</strong></p>` +
          `<p>  <strong>${item.serialization === 0 ? '' : '' }</strong></p>`;
      const warnInfo = `<p><strong style="color: red"></strong></p>`;
      const checkInfo = item.download_switch === 0 ? `<p><strong style="color: orangered"></strong></p>` : `<p><strong style="color: green"></strong></p>`;

      return item.serialization === 0 ? info + warnInfo + checkInfo : info + checkInfo;
    }
  }
}
</script>

