<template>
  <div class="announceStage" @mouseover = "showText" @mouseout = "hideText" @click = "handleClick">
    <svg class="icon bi" width="30" height="30" :fill="color" id = "announceIcon">
      <use :xlink:href="require('/node_modules/bootstrap-icons/bootstrap-icons.svg')+'#exclamation-circle'"></use>
    </svg>
    <transition name = "announceShow">
      <p v-show="isHover" id = 'announceContent'></p>
    </transition>
    <el-dialog
        title="VanIsLord-Manga  V2.0.0 "
        :visible.sync="isClicked"
        :append-to-body = true
        top = "5vh"
        width="55%"
        center>
      <hr>
      <div class = "text">
        <span>VanIsLord-Manga</span><br><br>
        <span></span><br><br>
        <span>V2.0.0&Kavita</span><br><br>
        <span></span><br><br>
        <span>V2.0.0</span><br><br>
        <span>1. KavitaKavita</span><br>
        <span>2. </span><br>
        <span>3. </span><br>
        <span>4. </span><br>
        <span>5. FlareSolverrBypasserCloudFlare</span><br>
        <span>6. </span><br>
        <span>7. </span><br><br><br><br>
        

        <span>Van/Alen Qi,,bug</span><br><br>
        <span></span><br><br><br><br>
        <span>VanIsLord-Manga</span><br><br>
        <span style="float: right"><em>--Tom "Van" Wang & Kun "Alen" Qi</em></span>

      </div>

      <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="isClicked = false"> </el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Announcement",
  data(){
    return{
      color: "#ded12f",
      isHover:false,
      isClicked:false
    }
  },
  methods:{
    showText(){
      this.isHover = true;
    },
    hideText(){
      this.isHover = false;
    },
    handleClick(){
      this.isClicked = true;
    }
  },
  watch:{
    'isHover':{
      handler(val){
        if(val){
          const body = document.querySelector("body");
          body.style.overflow = "hidden";
        }else{
          const body = document.querySelector("body");
          body.style.overflow = "hidden";
        }
      }
    }
  }

}
</script>

<style scoped>
.announceStage{
  width: 35px;
  height: max-content;
  text-align: center;
  overflow: visible;
}

#announceContent{
  color: white;
  margin: 0;
  transition: all 0.2s ease-in-out;
  overflow: hidden;
}

#announceContentHover {
  overflow: hidden;
  height: 0;
  color: white;
  margin: 0;
  transition: all 0.2s ease-in-out;
}
#announceIcon{
  transition: all 0.2s ease-in-out;
}

.announceStage:hover > #announceIcon{
  transform: scale(1.2);
  filter: drop-shadow(0px 0px 20px #f6bc06);
}

.text{
  font-size: 1.15em;
  color: black;
}

.announceShow-enter-active,.announceShow-leave-active{
  transition: all .1s ease-in-out;
}
.announceShow-enter,.announceShow-leave-to{
  transform: translateY(30px);
  opacity: 0;

}
</style>