<template>
  <!--  -->
  <div v-if="!this.$store.state.isPhone" class="kavitaCheckStage">
    <div class="kavitaUserLogin">
      <p class="kavitaTitle">Kavita</p>
      <el-input v-model="username" :disabled="!this.serverStatus" placeholder=""></el-input>
      <el-input v-model="password" :disabled="!this.serverStatus" placeholder="" show-password></el-input>
      <Vbotton :nameForButton = "''" :clickMethod = "login" :isIcon ="true" :iconClass = "'el-icon-s-promotion'"></Vbotton>
    </div>
    <div class="kavitaKanBan">
      <p class="kavitaStatus"><el-tag :type="this.aggregateStatus ? 'success' : 'danger'" effect="dark">{{this.aggregateStatus ? '' : ''}}</el-tag></p>
      <div :class="this.aggregateStatus ? 'kavitaInfoBorderSuccess' : 'kavitaInfoBorderFail'">
        <div class="kavitaUserInfo">
          <p class="kavitaTitle">Kavita</p>
          <el-result v-if="this.serverStatus" icon="success">
            <template slot="title">
              <p class="kavitaTitle"></p>
            </template>
          </el-result>
          <el-result v-else icon="error">
            <template slot="title">
              <p class="kavitaTitle"></p>
            </template>
            <template slot="subTitle">
              <p class="kavitaTitle"></p>
            </template>
          </el-result>
        </div>
        <div class="kavitaUserInfo">
          <p class="kavitaTitle"></p>
          <el-result v-if="this.possibleLoginStatus" icon="success">
            <template slot="title">
              <p class="kavitaTitle"></p>
            </template>
          </el-result>
          <el-result v-else icon="error">
            <template slot="title">
              <p class="kavitaTitle"></p>
            </template>
            <template slot="subTitle">
              <p class="kavitaTitle"></p>
            </template>
          </el-result>
        </div>
      </div>
    </div>

  </div>
  <!--  -->
  <div v-else class="mobileKavitaCheckStage">
    <div class="mobileKavitaUserLoginCard">
      <div class="mobileKavitaUserLoginTitleStage">
        <p class="mobileKavitaUserLoginTitle">KAVITA </p>
      </div>
      <div class="mobileInfoEnter">
        <van-field v-model="username" label="" placeholder="" />
        <van-field v-model="password" label="" type="password" placeholder="" />
      </div>
      <van-button round :color="'#A85DC3'" type="info" @click="login"></van-button>
    </div>

    <div class="mobileKavitaKanBan">
      <p class="mobileKavitaStatus"><el-tag :type="this.aggregateStatus ? 'success' : 'danger'" effect="dark">{{this.aggregateStatus ? '' : ''}}</el-tag></p>
      <div :class="this.aggregateStatus ? 'mobileKavitaInfoBorderSuccess' : 'mobileKavitaInfoBorderFail'">
        <div class="mobileKavitaUserInfo">
          <el-result v-if="this.serverStatus" icon="success">
            <template slot="title">
              <p class="mobileKavitaTitle"></p>
            </template>
          </el-result>
          <el-result v-else icon="error">
            <template slot="title">
              <p class="mobileKavitaTitle"></p>
            </template>
            <template slot="subTitle">
              <p class="mobileKavitaTitle"></p>
            </template>
          </el-result>
        </div>
        <div class="mobileKavitaUserInfo">
          <el-result v-if="this.possibleLoginStatus" icon="success">
            <template slot="title">
              <p class="mobileKavitaTitle"></p>
            </template>
          </el-result>
          <el-result v-else icon="error">
            <template slot="title">
              <p class="mobileKavitaTitle"></p>
            </template>
            <template slot="subTitle">
              <p class="mobileKavitaTitle"></p>
            </template>
          </el-result>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import Vbotton from "@/components/V-botton";
import {Notify} from "vant";

export default {
  name: "KavitaCheckPage",
  components: {Vbotton},
  data(){
    return{
      username:"",
      password:"",
      aggregateStatus: false,
      serverStatus: false,
      possibleLoginStatus: false,
    }
  },

  methods:{
    async login(){
      const loginLoading = this.$loading({
        lock: true,
        text: '...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      this.$http.post("kavita/login", {
        username: this.username,
        password: this.password,
      }).then((data) => {
        let res = data.data;
        if(res.code === 200){
          localStorage.setItem("apiKey", res.data.apiKey);
          localStorage.setItem("autoLogin", JSON.stringify(true));
          const iframe = document.createElement('iframe');
          iframe.src = `${window.KAVITA_EXPOSE_URL}/login?apiKey=${res.data.apiKey}`;
          iframe.style.display = 'none';
          iframe.onload = () => {
            localStorage.setItem("lastLoginTime", JSON.stringify(Date.now()));
            this.$store.commit("updateIsLoginAlready", true);
            this.possibleLoginStatus = true;
            if(this.$store.state.isPhone){
              Notify({ type: 'success', message: `, ` });
            } else {
              this.$notify({
                title: ``,
                message: ``,
                type: 'success',
                position: 'bottom-right'
              });
            }
            this.username = "";
            this.password = "";
            loginLoading.close();
          };
          document.body.appendChild(iframe);
        }
        else if(res.code === 434){
          if(this.$store.state.isPhone){
            Notify({type: 'danger', message: `Kavita`});
          } else {
            this.$notify({
              title: `Kavita`,
              message: ``,
              type: 'error',
              position: 'bottom-right'
            });
          }
          this.password = "";
          loginLoading.close();
        }
        else if(res.code === 500){
          if(this.$store.state.isPhone){
            Notify({type: 'danger', message: ``});
          } else {
            this.$notify({
              title: ``,
              message: ``,
              type: 'error',
              position: 'bottom-right'
            });
          }
          this.password = "";
          loginLoading.close();
        }
      }).catch((error) => {
        loginLoading.close();
        localStorage.removeItem('apiKey');
        localStorage.removeItem('lastLoginTime');
        localStorage.removeItem("autoLogin");
        this.$store.commit("updateIsLoginAlready", false);
        this.username = "";
        this.password = "";
        if(this.$route.fullPath !== "/") {
          this.$router.replace("/");
        }
        this.$message.error('Van Code:' + error.message);
      })
    }
  },

  watch:{
    possibleLoginStatus(val){
      this.aggregateStatus = this.serverStatus && val;
    }
  },

  async mounted() {
    const lastLoginTime = JSON.parse(localStorage.getItem("lastLoginTime"));
    const serverStatus = JSON.parse(localStorage.getItem("serverStatus"));
    const serverStatusLastCheckTime = JSON.parse(localStorage.getItem("serverStatusLastCheckTime"));
    const tenDaysInMs = 10 * 24 * 60 * 60 * 1000;
    const currentTime = Date.now();

    // 
    if((currentTime - lastLoginTime) > tenDaysInMs){
      this.possibleLoginStatus = false;
    }
    else{
      this.possibleLoginStatus = true;
    }

    //
    if(!serverStatus || ((currentTime - serverStatusLastCheckTime) > tenDaysInMs)){
      const checkLoading = this.$loading({
        lock: true,
        text: '...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });

      this.$http.get("kavita/status").then((data) => {
        let res = data.data;
        if(res.code === 200){
          this.serverStatus = true;
          this.aggregateStatus = this.serverStatus && this.possibleLoginStatus;
          localStorage.setItem("serverStatus", "true");
        }
        else if(res.code === 404){
          this.serverStatus = false;
          this.aggregateStatus = false;
          this.possibleLoginStatus  = false;
          localStorage.setItem("serverStatus", "false");
          localStorage.removeItem('serverStatusLastCheckTime');
        }
        localStorage.setItem("serverStatusLastCheckTime", JSON.stringify(Date.now()));
        this.$store.commit("updateIsLoginAlready", this.aggregateStatus);
        checkLoading.close();
      }).catch((error) => {
        checkLoading.close();
        localStorage.removeItem('serverStatus');
        localStorage.removeItem('serverStatusLastCheckTime');
        this.$store.commit("updateIsLoginAlready", false);
        if(this.$route.fullPath !== "/") {
          this.$router.replace("/");
        }
        this.$message.error('Van Code:' + error.message);
      })
    }
    else{
      this.serverStatus = serverStatus;
      this.aggregateStatus = this.serverStatus && this.possibleLoginStatus;
      this.$store.commit("updateIsLoginAlready", this.aggregateStatus);
    }
  },

}
</script>

